import Vue from "vue";
import VueRouter from "vue-router";
import Calc from "../views/Calculate.vue";

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        name: "Расчет",
        component: Calc,
    },
    {
        path: "/dict/:name",
        name: "dict",
        props: true,
        component: () => import(/* webpackChunkName: "base_units" */ "../views/Dict.vue"),
    },
    {
        path: "/dict/:name/:id",
        name: "child-dict",
        props: true,
        component: () => import(/* webpackChunkName: "base_units" */ "../views/Dict.vue"),
    },
];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes,
});

export default router;
